body {
  margin: 0px;
  padding: 0px; }

#app #start-overlay {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: white;
  -webkit-transition: top 0s 1s, background-color 0.5s 0.5s;
  -o-transition: top 0s 1s, background-color 0.5s 0.5s;
  transition: top 0s 1s, background-color 0.5s 0.5s; }
  #app #start-overlay.interacted {
    top: -100%;
    background-color: transparent; }
    #app #start-overlay.interacted #start-button {
      color: transparent;
      opacity: 0; }
  #app #start-overlay #start-button {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    font-size: 14px;
    font-family: Courier New, sans-serif;
    font-weight: 100;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: black;
    opacity: 1;
    -webkit-transition: opacity 0.5s, color 0s;
    -o-transition: opacity 0.5s, color 0s;
    transition: opacity 0.5s, color 0s;
    cursor: pointer; }
    #app #start-overlay #start-button::after {
      content: " ";
      display: block;
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      margin: -2px;
      border-radius: 50%;
      border: 2px solid;
      border-color: black transparent black transparent;
      -webkit-animation: start-button-spin 1.5s linear infinite;
              animation: start-button-spin 1.5s linear infinite; }

@-webkit-keyframes start-button-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes start-button-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



